@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn{border-radius:20px}.btn.btn--primary{background-color:#1976d2}.btn.btn--danger{background-color:#ff3131}.btn.btn--success{background-color:#38fd2d}.btn.btn--warning{background-color:#fda41d}.header__wrap-icon{display:contents}.header__icon-back{color:#fff}html{height:100%;overflow-x:hidden}html body{height:100%;font-family:"Cairo",sans-serif;overflow-x:clip}
